import $http from '@/api';

const SET_ALL_LINKS = 'SET_ALL_LINKS';

export default {
  namespaced: true,
  state: {
    links: [],
  },
  mutations: {
    [SET_ALL_LINKS](state, payload) {
      state.links = payload;
    }
  },
  actions: {
    async getAllLinks({ commit }) {
      try {
        const { data } = await $http.get('/links');

        commit(SET_ALL_LINKS, data);

        return { data, error: null };
      } catch (error) {
        const {
          data: { message },
          status,
        } = error.response;

        return { data: null, error: { message, status } };
      }
    }
  },
  getters: {
    allLinks(state) {
      return state;
    }
  }
};